const API_PREFIX = '/v1/'

function getBaseURL() {
    return window.location.protocol + '//' + window.location.hostname + ':' + window.location.port + API_PREFIX;
}

var Constants = {
    baseUrl: getBaseURL(),
    LoadingImage: '/static/images/ajax-loader.gif'
}

export default Constants