import React, {Component} from 'react';
import {T, saveSelection, getSelection} from './Utils'
import Snaps from './Snaps'

class Applications extends Component {
    constructor(props) {
        super(props)
        this.state = {
            snaps: [],
        };

        let selected = getSelection()
        if (selected && selected.snaps && selected.snaps.length > 0) {
            this.state.snaps = selected.snaps
        }
    }
    renderSelected() {
        let image ='/static/images/' + this.props.board.id + '.png'
        return (
            <div className="row">
                <div className="col-1">
                    <img src={image} alt={this.props.board.name} width="100px" />
                </div>
                <div className="col-5 u-align--bottom">
                    <h4>{this.props.board.name}</h4>
                    <p>Ubuntu {T(this.props.os.type)} {this.props.os.version}</p>
                </div>
            </div>
        )
    }

    handleSnapInstall = (snap) => {
        // Check we don't have the snap
        if (this.state.snaps.indexOf(snap) >= 0) {
            return
        }

        let snaps = this.state.snaps
        snaps.push(snap)
        this.setState({snaps: snaps})
    }

    handleSnapOnChange= (e) => {
        e.preventDefault();
        this.setState({snapName: e.target.value});
    }

    handleDialogCancel = (e) => {
        e.preventDefault();
        //this.setState({showInstall: false});
    }

    handleRemove = (e) => {
        e.preventDefault();
        let snap = e.target.getAttribute('data-key')
        let snaps = this.state.snaps.filter(s => {
            return s !== snap
        })
        this.setState({snaps: snaps});
    }

    handleConfirm = (e) => {
        e.preventDefault();

        // Save the settings as JSON string to local storage
        let board = {
            board: this.props.board,
            os: this.props.os,
            snaps: this.state.snaps
        }
        saveSelection(board)

        // Navigate to the confirmation page
        window.location.href = '/confirm'
    }

    render() {
        return (
            <div>
                {this.renderSelected()}

                <h2>{T('choose-applications')}</h2>
                <p>{T('choose-applications-desc')}</p>

                <div className="row">
                    <div className="p-card col-4">
                        <p>{T('pre-installed-snaps')}</p>
                        <ul className="p-list">
                        {this.state.snaps.map(s => {
                            return (
                                <li className="p-list__item">
                                    <button data-key={s} className="p-button--neutral small" title={T("remove-application")} onClick={this.handleRemove}>
                                        {T('remove')}
                                    </button>
                                    &nbsp; {s}
                                </li>
                            )
                        })}
                        </ul>
                    </div>
                    <Snaps message={T('find-snaps')}
                           handleTextChange={this.handleSnapOnChange}
                           handleInstallClick={this.handleSnapInstall}
                           handleCancelClick={this.handleDialogCancel} />
                </div>

                <div>
                    <button className="p-button--brand" onClick={this.handleConfirm}>{T('confirm')}</button>
                </div>
            </div>
        )
    }
}

export default Applications