import React, {Component} from 'react';
import {T, getSelection} from "./Utils";

class Confirm extends Component {
    constructor(props) {
        super(props)
        this.state = {
            messages: [],
        };
    }
    handleBuild = (e) => {
        setTimeout(() => {
            const items = ['ogra', 'ondra', 'alfonso', 'konrad']
            let item = items[Math.floor(Math.random()*items.length)];
            this.setState({messages: ['Waking trained chef... ' + item]})
        }, 1000)
        setTimeout(() => {
            let m = this.state.messages
            m.push('Cranking the build machine...')
            this.setState({messages: m})
        }, 2000)
        setTimeout(() => {
            let m = this.state.messages
            m.push('This is just a demo, but you get the idea')
            this.setState({messages: m})
        }, 3000)
    }

    renderSelected() {
        let image ='/static/images/' + this.props.board.board.id + '.png'
        return (
            <div className="row">
                <div className="col-1">
                    <img src={image} alt={this.props.board.board.name} width="100px" />
                </div>
                <div className="col-5 u-align--bottom">
                    <h4>{this.props.board.board.name}</h4>
                    <p>Ubuntu {T(this.props.board.os.type)} {this.props.board.os.version}</p>
                    <p>{this.props.board.snaps.length !== 1 ? this.props.board.snaps.length + ' snaps': '1 snap'}</p>
                </div>
            </div>
        )
    }

    renderConsole() {
        if (this.state.messages.length === 0) {
            return
        }
        return (
            <pre className="console">
                {this.state.messages.map(m => {
                    return m + '\n'
                })}
            </pre>
        )
    }

    render() {
        let selected = this.props.board
        return (
            <div className="first">
                {this.renderSelected()}

                <h2>{T('confirm-configuration')}</h2>

                <div className="row">
                    <pre>
                        <code>
                            board:{'\n'}
                            {}  name: {selected.board.name}{'\n'}
                            {}  os:{'\n'}
                            {}    type: {selected.os.type}{'\n'}
                            {}    version: {selected.os.version}{'\n'}
                            {}  snaps:{'\n'}
                            {selected.snaps.map(s => {
                                return '    ' + s + '\n'
                            })}
                        </code>
                    </pre>
                </div>

                <div>
                    <h3>{T('ready-to-build')}</h3>
                    <button className="p-button--brand" onClick={this.handleBuild}>{T('build')}</button>
                </div>

                <div className="row">
                    {this.renderConsole()}
                </div>
            </div>
        );
    }
}

export default Confirm;