import React, {Component} from 'react';
import {T} from './Utils'

class OS extends Component {
    handleClick = (e) => {
        window.location.href = '/boards/' + this.props.board.id + '/' + e.target.getAttribute('data-key')
    }

    renderOS() {
        return this.props.board.os.map(o => {
            let id=o.type + o.version
            return (
                <div className="os" key={id} data-key={id} onClick={this.handleClick}>
                    <img src="/static/images/ubuntu.png" alt="Ubuntu" data-key={id} />
                    <h4 data-key={id}>{T(o.type)}</h4>
                    <p data-key={id}>{T(o.version)}</p>
                </div>
            )
        })
    }

    render() {
        let image ='/static/images/' + this.props.board.id + '.png'
        return (
            <div className="first">
                <div className="row">
                    <div className="col-1">
                        <img src={image} alt={this.props.board.name} width="100px" />
                    </div>
                    <div className="col-5 u-align--bottom">
                        <h4>{this.props.board.name}</h4>
                    </div>
                </div>

                <h2>{T('choose-os')}</h2>
                <p>{T('choose-os-desc')}</p>

                {this.renderOS()}
            </div>
        );
    }
}


export default OS;